.multi-question-survey-container {
  padding: 20px;
  max-width: 900px;
  margin: 0 auto;
}

.question-groups-container {
  display: flex;
  flex-direction: column;
  gap: 30px;
}

.question-group {
  background-color: #f9f9f9;
  border-radius: 8px;
  padding: 20px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.05);
}

.group-header {
  margin-bottom: 20px;
  border-bottom: 1px solid #eee;
  padding-bottom: 15px;
}

.group-title {
  font-size: 22px;
  font-weight: 600;
  color: #333;
  margin: 0 0 10px 0;
}

.group-description {
  color: #666;
  font-size: 16px;
  margin: 0;
}

.group-questions {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.ungrouped-questions {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.question-item {
  transition: transform 0.2s, box-shadow 0.2s;
}

.question-item:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.survey-navigation {
  margin-top: 40px;
  display: flex;
  justify-content: center;
}

.survey-submit-button {
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 4px;
  padding: 12px 30px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: background-color 0.2s;
}

.survey-submit-button:hover {
  background-color: #45a049;
}

.survey-submit-button:disabled {
  background-color: #cccccc;
  cursor: not-allowed;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .multi-question-survey-container {
    padding: 15px;
  }
  
  .question-group {
    padding: 15px;
  }
  
  .group-title {
    font-size: 20px;
  }
  
  .survey-submit-button {
    width: 100%;
  }
}