.category-container-parent {
    margin: 0.5em;
    border-radius: 2px;
    /* border: 1px solid lightgrey; */
    /* width: 220px; */
}

.category-container-parent.vote{
    margin-top: 0em;
    margin-bottom: 2em;
}

.category-container-parent.organize {
    /* margin: 2em; */
    border-radius: 2px;
    min-height: 15em;
    margin-bottom: 1em;
    /* max-height: 110em; */
}

/* Add clear spacing for bins in organize view */
.category-container-parent.organize.Positive,
.category-container-parent.organize.Negative,
.category-container-parent.organize.Neutral {
    margin-top: 0;
}

.category-container-parent.organize.Undecided {
    margin-top: 0em;
}


.skipped-container{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.category-container-parent.organize.Skip {
    margin-top: 0em;
    margin-bottom: 0em;
}

.skipped-panel{
    background-color: #ddd;
    width: 80%;
}

.category-toggle {
    background-color: #ddd;
    width: 40%;
}

.categoryContainer {
    border-radius: 2px;
    height: 100%;
    /* width: 220px; */
    /* margin: 2em; */
    /* border: 1px solid lightgrey; */

    -ms-overflow-style: none;
    /* Internet Explorer and Edge */
    scrollbar-width: none;
    scrollbar-color: lightgrey;
    -webkit-overflow-scrolling: touch;
}

.categoryContainer.organize{
    width: 95%;
}

.categoryContainer.organize.Positive.scroll, 
.categoryContainer.organize.Negative.scroll, 
.categoryContainer.organize.Neutral.scroll {
    overflow: scroll;
}



.categoryContainer::-webkit-scrollbar {
    width: 0.2em;
    background-color: transparent;
}

.categoryContainer::-webkit-scrollbar-thumb {
    background-color: lightgrey;
    border-radius: 10px;
    border: 4px solid transparent;
}

.categoryContainer::-webkit-scrollbar-track {
    background-color: transparent;
    border-radius: 10px;
}


/* Default styles (fallback / large screen) */
.scrollable-bins .category-container-parent.organize.Positive,
.scrollable-bins .category-container-parent.organize.Negative,
.scrollable-bins .category-container-parent.organize.Neutral {
  flex-shrink: 0;
  display: inline-block;
  scroll-snap-align: center;
  transition: all 0.3s ease;
  position: relative;
  margin-right: 1em;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
  border-radius: 8px;
  padding: 0.5em;
  background-color: #fff;
  width: 31%;
  min-width: 350px;
}

/* Mobile screens (<768px): Show 1 bin, hint next */
@media (max-width: 767px) {
  .scrollable-bins {
    padding-left: 0vw;
    padding-right: 0vw;
  }

  .scrollable-bins .category-container-parent.organize.Positive,
  .scrollable-bins .category-container-parent.organize.Negative,
  .scrollable-bins .category-container-parent.organize.Neutral {
    width: 90vw;
    min-width: 90vw;
    max-width: 90vw;
    margin-right: 10px;
  }
}

/* Tablets (768px–1023px): Show 2 bins side-by-side */
@media (min-width: 768px) and (max-width: 1023px) {
  .scrollable-bins {
    padding-left: 4vw;
    padding-right: 4vw;
  }

  .scrollable-bins .category-container-parent.organize.Positive,
  .scrollable-bins .category-container-parent.organize.Negative,
  .scrollable-bins .category-container-parent.organize.Neutral {
    width: 48vw;
    min-width: 48vw;
    max-width: 48vw;
    margin-right: 1em;
  }
}

/* Desktop (≥1024px): Fit 3 bins cleanly */
@media (min-width: 1024px) {
  .scrollable-bins .category-container-parent.organize.Positive,
  .scrollable-bins .category-container-parent.organize.Negative,
  .scrollable-bins .category-container-parent.organize.Neutral {
    width: 31%;
    min-width: 350px;
    /* max-width: 450px; */
    margin-right: 0.5em;
  }
}

/* Desktop (≥1600px): Super large screens */
@media (min-width: 1600px) {

    .scrollable-bins {
      justify-content: center;
    }
  
    .scrollable-bins .category-container-parent.organize.Positive,
    .scrollable-bins .category-container-parent.organize.Negative,
    .scrollable-bins .category-container-parent.organize.Neutral {
      width: 31%;
      min-width: 350px;
      /* max-width: 450px; */
      margin-right: 0.5em;
    }
  }

/* Make category headers draggable */
.category-container-parent.organize h2 {
    cursor: grab;
    user-select: none;
    position: relative;
    padding-left: 1.5em;
}

.category-container-parent.organize h2::before {
    content: "";
    position: absolute;
    left: 0.25em;
    top: 50%;
    transform: translateY(-30%);
    width: 1em;
    height: 1em;
    background-size: contain;
    background-repeat: no-repeat;
    opacity: 0.7;
}

.category-container-parent.organize.Undecided {
    min-height: 7em;
    z-index: 10;
    background-color: #fff;
}

.category-container-parent.organize.Skip {
    /* width: 100%; */
    /* max-height: 7em; */
    min-height: 0em;
}

