/* Hide scrollbar but keep functionality */
.scrollable-bins::-webkit-scrollbar {
    display: none;
}

/* Responsive styles for mobile devices */
@media (max-width: 768px) {
    /* Show the swipe hint on mobile */
    .mobile-swipe-hint {
        display: block;
    }
    
    /* Show part of the next bin to hint at more content */
    .scrollable-bins {
        padding-left: 5vw;
        padding-right: 5vw;
        overflow-x: auto;
        scroll-snap-type: x mandatory;
    }

    /* Adjust bin sizes for better mobile viewing */
    .scrollable-bins .category-container-parent.organize.Positive,
    .scrollable-bins .category-container-parent.organize.Negative,
    .scrollable-bins .category-container-parent.organize.Neutral {

        width: 85%;
        min-width: 85%;
        /* margin-right: 15px; */
        scroll-snap-align: center;
    }
    
    /* Adjust card section for mobile */
    .card-section {
        padding: 0.5em;
    }
    
    /* Make bin separator more compact */
    .bin-separator {
        height: 3em;
        margin: 0.5em 0;
    }
}

.categoryCanvas.organize {
    display: flex;
    overflow-x: auto;
    white-space: nowrap;
    margin-left: -1.5em;
    margin-top: 1em; /* Space between undecided card and bins */
    scroll-snap-type: x mandatory;
    scrollbar-width: none; /* Hide scrollbar for Firefox */
    -webkit-overflow-scrolling: touch;
    padding-bottom: 10px; /* Space for the scrollbar */
    position: relative;
    width: calc(100% + 1.5em); /* Add extra width to account for margin-left */
    padding-left: 5%;
    padding-right: 5%;
}

/* Hide scrollbar for Chrome, Safari and Opera */
.categoryCanvas.organize::-webkit-scrollbar {
    display: none;
}

/* Remove scroll visibility but keep functionality */
.categoryContainer.organize {
    /* overflow-y: auto; */
    height: auto !important;
    -ms-overflow-style: none;
    scrollbar-width: none;
}

.categoryContainer.organize::-webkit-scrollbar {
    display: none;
}

/* Add clearer scroll indicators */
.categoryCanvas.organize {
    position: relative;
}

/* Move scroll hint to category separator */
.category-separator::after {
    content: "← Swipe or drag to scroll through bins →";
    position: absolute;
    background-image: none;
    width: auto;
    height: auto;
    border-radius: 4px;
    background-color: rgba(255, 255, 255, 0.9);
    box-shadow: 0 2px 8px rgba(0,0,0,0.15);
    font-size: 0.85em;
    color: #666;
    padding: 5px 10px;
    bottom: 10px;
    left: 50%;
    transform: translateX(-50%);
    white-space: nowrap;
    z-index: 100;
    pointer-events: none;
}

/* Settings for the scrollable bin area */
.categoryCanvas.organize {
    padding-left: 10px;
    padding-right: 10px;
    z-index: 5; /* Lower z-index than the undecided card */
    background-color: #f8f8f8; /* Light background to differentiate from the top section */
    border-top: 1px solid #eee;
    padding-top: 1em;
}

.categoryCanvas.vote {
    width: 78%;
    margin-left: -1.5em;
}

