.reorder {
    height: 24px;
    width: 8em;
    font-size: 14px;
    background-color: rgba(255, 255, 255, 0.2); /* light translucent background */
    color:rgba(54, 54, 54, 0.8); /* lighter white for passive feel */
    border: 1px solid rgba(255, 255, 255, 0.3); /* soft border */
