  .vote-current-state {
    min-width:25%;
    /* width: 25%; */
    /* margin-top: 0.5em; */
    display: flex;
    justify-content: center;
    align-items: center;
  }
    
  .vote-current-state .vote,
  .vote-current-state .cost {
      width: 40%;
      text-align: center;
  }
  
  /* ================================================
     Credit Summary Box
     ================================================ */
  .credit-summary-box {
    flex: 0 0 auto;
    width: 180px;
    padding: 10px 15px;
    border: 1px solid #eee;
    border-radius: 4px;
    margin-left: 15px;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
  }
  
  .credit-summary-title {
    font-size: 14px;
    font-weight: 600;
    color: #444;
    margin-bottom: 8px;
    padding-bottom: 5px;
    border-bottom: 1px solid #ddd;
  }
  
  .credit-summary-row {
    display: flex;
    justify-content: space-between;
    margin: 5px 0;
    font-size: 13px;
  }
  
  .credit-value {
    font-weight: 600;
    color: #333;
  }
  
  /* ================================================
     Additional Responsive Tweaks ( < 768px )
     ================================================ */
  @media (max-width: 768px) {
    .item-wrapper.organize.Undecided {
      flex-direction: column;
      align-items: stretch;
      min-height: auto;
    }
  
    .item-wrapper.organize.Undecided .optionCard {
      flex-direction: column;
      align-items: flex-start;
      width: 100%;
      padding: 12px;
      box-sizing: border-box;
    }
  
    .organizer-info {
      flex: 1 1 auto;
      margin: 8px;
      /* If you want the text to fill height as well, add align-items or remove top margin. */
      display: flex;
      flex-direction: column;
      justify-content: center; /* So text is vertically centered. */
    }
  
    .item-wrapper.organize .organizer-info-title {
      font-size: 18px;
      font-weight: 600;
      line-height: 1.3;
      padding-bottom: 10px;
      white-space: normal;
    }
  
    .item-wrapper.organize .organizer-info-des {
      font-size: 15px;
      line-height: 1.5;
      max-height: none;
      overflow: visible;
      /* padding-bottom: 15px; */
    }
  
    .organizer-info-title {
      font-size: 16px;
      line-height: 1.2;
      padding-bottom: 5px;
    }
  
    .organizer-info-des,
    .organizer-info-des-light {
      font-size: 14px;
      line-height: 1.4;
      padding-bottom: 10px;
    }
  
    .item-wrapper.organize .controller-panel {
      display: flex;
      flex-wrap: wrap;
      gap: 10px;
      width: 100%;
      padding: 0 15px 15px;
      box-sizing: border-box;
      margin-top: -0.5em;
    }
  
    .item-wrapper.organize .category-button {
      width: 45%;
      min-width: 80px;
      height: 44px;
      font-size: 15px;
      margin: 0;
      box-sizing: border-box;
    }
  
    .vote-current-state {
      min-width: 40%;
      width: calc(100% - 20px);
      margin: 0 10px 8px;
    }
  
    .vote-interaction-area {
      flex-direction: column;
      align-items: center;
      width: 100%;
    }
  
    .vote-display {
      display: flex;
      justify-content: center;
      width: 80%;
      max-width: 100%;
      margin: 0 auto 10px;
      position: relative; /* needed if you use ::after for “Tap to change” text */
    }
  
    /* .vote-current-state .vote,
    .vote-current-state .cost {
      font-size: 12px;
    } */
  
    .vote-display.active .vote-current-state {
      background-color: #eef8ff;
      border-color: #27AAFE;
    }
  
    .vote-display::after {
      content: "Tap to change";
      position: absolute;
      top: -18px;
      right: 5px;
      font-size: 11px;
      color: #666;
      background: #fff;
      padding: 2px 6px;
      border-radius: 10px;
      opacity: 0;
      pointer-events: none;
      transition: opacity 0.3s ease;
    }
  
    .vote-display:active::after {
      opacity: 1;
    }
  }
  
  /* ================================================
     Very Narrow Screens ( < 480px )
     ================================================ */
  @media (max-width: 480px) {
    .item-wrapper.organize.Undecided .controller-panel {
      flex-direction: column;
    }
    .item-wrapper.organize.Undecided .category-button {
      width: 100%;
      min-width: 0;
      margin: 4px 0;
    }
  }
  