/* Main survey question title */
.surveyQuestionTitle {
    font-size: 2rem;
    font-weight: bold;
    margin-bottom: 0.5em;
    color: #222;
    flex-grow: 1;
  }
  
  /* Additional instruction line (e.g., "Select all that apply") */
  .surveyQuestionInstructions {
    font-size: 1rem;
    font-style: italic;
    color: #666;
    margin-bottom: 0.5em;
  }
  
  /* Optional description with more detail or background */
  .surveyQuestionDescription {
    font-size: 0.95rem;
    color: #444;
    margin-bottom: 1em;
    line-height: 1.4;
  }
  
  /* The actual question text or prompt (if separate from title) */
  .surveyQuestionText {
    font-size: 1.1rem;
    font-weight: 500;
    color: #333;
    margin-bottom: 1em;
  }

  .next {
    white-space: nowrap;
  }

  .title-bar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 1em;
  }

  
    .hint-message {
        text-align: right;
        color: #FF7F50;
        margin-top: -1em;
        /* width: 40%; */
    }

    .h2 {
        margin-bottom: -1em;
    }

    .video-container {
        position: relative;
        padding-bottom: 56.25%; /* 16:9 aspect ratio */
        height: 0;
        overflow: hidden;
        margin: 1.5em 0;
        border-radius: 8px;
        box-shadow: 0 2px 8px rgba(0,0,0,0.1);
      }
      
      .video-container iframe {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
      }
      