.likert-question-container {
  background-color: #ffffff;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
  padding: 20px;
  margin-bottom: 20px;
}

.likert-question-header {
  margin-bottom: 20px;
}

.likert-question-title {
  font-size: 18px;
  font-weight: 600;
  color: #333;
  margin-bottom: 10px;
  margin-top: 0;
}

.likert-question-description {
  font-size: 14px;
  color: #666;
  margin-top: 8px;
}

.likert-scale-container {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.likert-scale-labels {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}

.likert-min-label, .likert-max-label {
  font-size: 14px;
  color: #666;
  font-weight: 600;
}

.likert-scale-options {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-top: 5px;
}

.likert-option {
  text-align: center;
  flex: 1;
}

.likert-option-label {
  display: flex;
  flex-direction: column;
  align-items: center;
  cursor: pointer;
}

.likert-option-text {
  margin-top: 5px;
  font-size: 14px;
  color: #333;
}

.likert-option input[type="radio"] {
  margin: 0;
  cursor: pointer;
  height: 18px;
  width: 18px;
}

.likert-option input[type="radio"]:checked + .likert-option-text {
  font-weight: 600;
  color: #4a90e2;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .likert-scale-options {
    overflow-x: auto;
    padding-bottom: 10px;
  }
  
  .likert-option {
    min-width: 50px;
  }
}