.text-question-container {
  background-color: #ffffff;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
  padding: 20px;
  margin-bottom: 20px;
}

.text-question-header {
  margin-bottom: 20px;
}

.text-question-title {
  font-size: 18px;
  font-weight: 600;
  color: #333;
  margin-bottom: 10px;
  margin-top: 0;
}

.text-question-description {
  font-size: 14px;
  color: #666;
  margin-top: 8px;
}

.text-input-container {
  width: 100%;
}

.text-input, .text-textarea {
  width: 100%;
  padding: 12px;
  font-size: 16px;
  border: 1px solid #ddd;
  border-radius: 4px;
  background-color: #f9f9f9;
  transition: border-color 0.2s, box-shadow 0.2s;
}

.text-input:focus, .text-textarea:focus {
  border-color: #4a90e2;
  box-shadow: 0 0 0 3px rgba(74, 144, 226, 0.1);
  outline: none;
}

.text-textarea {
  resize: vertical;
  min-height: 100px;
}

.text-character-counter {
  text-align: right;
  margin-top: 8px;
  font-size: 12px;
  color: #666;
}

.text-counter-limit {
  color: #d32f2f;
  font-weight: 600;
}

.text-input:disabled, .text-textarea:disabled {
  background-color: #f1f1f1;
  cursor: not-allowed;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .text-question-container {
    padding: 15px;
  }
  
  .text-input, .text-textarea {
    font-size: 14px;
    padding: 10px;
  }
}