.site-footer {
  background-color: #f5f5f5;
  color: #555;
  padding: 1rem 0;
  margin-top: auto;
  font-size: 0.9rem;
  border-top: 1px solid #e0e0e0;
}

.footer-content {
  /* max-width: 1200px; */
  margin: 0 auto;
  padding: 0 1rem;
}

.footer-content.simple {
  text-align: center;
}

.footer-content.simple p {
  margin: 0.5rem 0;
}

.footer-bottom {
  text-align: center;
  padding-top: 0.5rem;
  margin-top: 0.5rem;
  border-top: 1px solid #eaeaea;
  font-size: 0.8rem;
}